import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { Container, Button, ButtonGroup } from 'react-bootstrap';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionAccounts, optionCustomerNavigator, optionMeasure, optionRange, optionRegion, optionMetro } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE_URL } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import RevenueContent from './RevenueContent';
import BookingsContent from './BookingsContent';
import OrdersContent from './OrdersContent';
import OpportunitiesContent from './OpportunitiesContent';

const Financials = () => {
    const [revenueData, setRevenueData] = useState([])
    const [bookingsData, setBookingsData] = useState([]);
    const [ordersData, setOrdersData] = useState([]);
    const [isLoading, setIsLoading] = useState(true)
    const [activeTab, setActiveTab] = useState(null);

    // Axios call
    const fetchData = () => {
        setIsLoading(true)
        axios({
            method: 'get',
            url: `${API_BASE_URL}/account-navigator/financials/revenue.json?print=pretty`,
        }).then(result => {
            setRevenueData(result.data);
            setIsLoading(false)
        }).catch(error => {
            console.log(error);
            setIsLoading(false)
        });
        axios({
            method: 'get',
            url: `${API_BASE_URL}/account-navigator/financials/bookings.json?print=pretty`,
        }).then(result => {
            setBookingsData(result.data);
            setIsLoading(false)
        }).catch(error => {
            console.log(error);
            setIsLoading(false)
        });
        axios({
            method: 'get',
            url: `${API_BASE_URL}/account-navigator/financials/orders.json?print=pretty`,
        }).then(result => {
            setOrdersData(result.data);
            setIsLoading(false)
        }).catch(error => {
            console.log(error);
            setIsLoading(false)
        });
    };

    useEffect(() => {
        fetchData();
    }, []);

    // responsive dropdowns
    const cardRef = useRef(null);
    const { width } = useWidowResize(cardRef);

    // show all filters
    const [showModal, setShowModal] = useState(false);
    const handleCloseModal = () => {
        setShowModal(false);
    }
    const handleShowModal = () => {
        setShowModal(true)
    };

    // comment area
    const [showComment, setShowComment] = useState(false);
    const handleCloseComment = () => {
        setShowComment(false);
    }
    const handleShowComment = () => {
        setShowComment(true)
    };

    if (isLoading) return <div className="w-100" ref={cardRef}><Loader /></div>;
console.log(revenueData)

    return (
        <Container className="account-navigator-page" fluid ref={cardRef} >
            <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
                <FilterContainer
                    className={`${width}`}
                    showComment={showComment}
                    handleShowModal={handleShowModal}
                    handleShowComment={handleShowComment}
                >
                    <div className="filters">
                        <ButtonGroup className="sub-tab">
                            <Button variant='light' onClick={() => {
                                setActiveTab('revenue')
                            }}>
                                Revenue
                            </Button>
                            <Button variant='light' onClick={() => {
                                setActiveTab('bookings')
                            }}>
                                Bookings
                            </Button>
                            <Button variant='light' onClick={() => {
                                setActiveTab('orders')
                            }}>
                                Orders
                            </Button>
                            <Button variant='light' onClick={() => {
                                setActiveTab('opportunities')
                            }}>
                                Opportunities
                            </Button>
                        </ButtonGroup>
                        <Separator height='39px' />
                        {/* <ResponsiveRadioDD
                                id="include_only"
                                addTitleOnWide
                                wide={width >= 1516 ? true : false}
                                narrow={width < 1516 ? true : false}
                                options={optionCustomerNavigator}
                            /> */}
                        <CheckboxDD
                            title="Region"
                            id="measeure"
                            options={optionRegion}
                        />
                        <SearchCheckboxDD
                            title="Metro"
                            id="metro"
                            options={optionMetro}
                        />
                        <RadioDD
                            title="Measure"
                            id="measeure"
                            options={optionMeasure}
                        />
                        <Separator height='39px' />
                        <ResponsiveRadioDD
                            title="Range by"
                            id="range2"
                            wide={width >= 1394 ? true : false}
                            narrow={width < 1394 ? true : false}
                            options={optionRange}
                        />
                    </div>

                    <FilterModal
                        title="Navigator"
                        setShowModal={setShowModal}
                        showModal={showModal}
                        handleCloseModal={handleCloseModal}
                    >
                        <ResponsiveRadioDD
                            id="accounts_modal"
                            options={optionAccounts}
                            visibleInModal
                        />
                        <ResponsiveRadioDD
                            id="include_only_modal"
                            options={optionCustomerNavigator}
                            visibleInModal
                        />
                    </FilterModal>
                </FilterContainer>

                {activeTab === 'revenue' && <RevenueContent revenueData={revenueData[0]} />}
                {activeTab === 'bookings' && <BookingsContent bookingsData={bookingsData[0]} />}
                {activeTab === 'orders' && <OrdersContent revenueData={revenueData[0]} />}
                {activeTab === 'opportunities' && <OpportunitiesContent revenueData={revenueData[0]}  />}

                <Comment
                    data={comments}
                    handleCloseComment={handleCloseComment}
                    showComment={showComment}
                />
            </div>
        </Container >
    );
};

export default Financials;
