import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Modal, Button, Alert } from 'react-bootstrap';
import { motion, AnimatePresence } from 'framer-motion';
import SearchCheckbox from '../../components/searchCheckbox/SearchCheckbox';
import { optionMetrics } from '../../constants/filterOptions';
import { ReactComponent as Loader } from '../../assets/icons/loader.svg';
import { ReactComponent as Asia } from '../../assets/icons/asia.svg';
import { ReactComponent as Building } from '../../assets/icons/building.svg';
import { ReactComponent as Flag } from '../../assets/icons/flag.svg';
import { ReactComponent as Globe } from '../../assets/icons/globe.svg';
import { ReactComponent as Profile } from '../../assets/icons/profile.svg';
import { ReactComponent as Team } from '../../assets/icons/team.svg';
import './snapshot.scss';

// animate
const animateStep = {
  visible: {
    opacity: 1,
    x: '0',
    transition: { duration: .5, bounce: 0 }
  },
  backEnter: {
    opacity: 0,
    x: '-100px',
    transition: { duration: .5, bounce: 0 }
  },
  backExit: {
    opacity: 0,
    x: '100px',
    transition: { duration: .5, bounce: 0 }
  },
  nextEnter: {
    opacity: 0,
    x: '100px',
    transition: { duration: .5, bounce: 0 }
  },
  nextExit: {
    opacity: 0,
    x: '-100px',
    transition: { duration: .5, bounce: 0 }
  }
}

const animateGenerate = {
  hidden: {
    opacity: 0
  },
  visible: {
    opacity: 1
  }
}

// loop
const step1List = [
  {
    icon: <Globe className="icon" />,
    key: 'global_report',
    label: 'GLOBAL REPORT',
  },
  {
    icon: <Asia className="icon" />,
    key: 'regional_report',
    label: 'REGIONAL REPORT',
  },
  {
    icon: <Flag className="icon" />,
    key: 'country_report',
    label: 'COUNTRY REPORT',
  },
  {
    icon: <Building className="icon" />,
    key: 'metro_report',
    label: 'METRO REPORT',
  },
  {
    icon: <Team className="icon" />,
    key: 'sales_team_report',
    label: 'SALES TEAM REPORT',
  },
  {
    icon: <Profile className="icon" />,
    key: 'coustomer_report',
    label: 'CUSTOMER REPORT',
  }
]

const step2List = [
  {
    icon: <Asia className="icon" />,
    key: 'USA',
    label: 'AMERICAS',
  },
  {
    icon: <Asia className="icon" />,
    key: 'EMEA',
    label: 'EUROPE, MIDDLE EAST, AND AFRICA',
  },
  {
    icon: <Asia className="icon" />,
    key: 'ASIA',
    label: 'ASIA PACIFIC',
  },
]

const step3List = [
  {
    key: 'last_completed_quarter',
    label: 'LAST COMPLETED QUARTER',
  },
  {
    key: 'last_year',
    label: 'LAST YEAR',
  },
  {
    key: 'year_to_date',
    label: 'YEAR-TO-DATE',
  },
  {
    key: 'last_4_quarters',
    label: 'LAST 4 QUARTERS',
  },
  {
    key: 'custom',
    label: 'CUSTOM',
  },
]

// state
const initialState = {
  step1: '',
  step2: '',
  step3: '',
  step4: []
}

const BuilderModal = (props) => {
  const { showBuilderModal, onCancelModal } = props;
  let history = useHistory();
  const [stepOpen, setStepOpen] = useState(1);
  const [clickBack, setClickBack] = useState(false);
  const [clickNext, setClickNext] = useState(false);
  const [generating, setGenerating] = useState(false);
  const [stepsSelectedValue, setStepsSelectedValue] = useState(initialState)
  const [showValidate, setShowValidate] = useState(false)

  const onSelectValueStep1 = (e) => {
    setShowValidate(false)
    setStepsSelectedValue({
      ...stepsSelectedValue,
      step1: e.target.id,
    })
  }

  const onSelectValueStep2 = (e) => {
    setShowValidate(false)
    setStepsSelectedValue({
      ...stepsSelectedValue,
      step2: e.target.id,
    })
  }

  const onSelectValueStep3 = (e) => {
    setShowValidate(false)
    setStepsSelectedValue({
      ...stepsSelectedValue,
      step3: e.target.id,
    })
  }

  const onCancel = async () => {
    await setClickBack(false)
    await setClickNext(false)
    await onCancelModal()
    setStepOpen(1)
    setStepsSelectedValue(initialState)
    setShowValidate(false)
  }

  const onBack = async () => {
    await setClickNext(false)
    await setClickBack(true)
    setStepOpen(stepOpen - 1)
  }

  const onNext = async () => {
    await setClickBack(false)
    await setClickNext(true)
    if(stepOpen === 1){
      if(stepsSelectedValue.step1 !== '') setStepOpen(stepOpen + 1)
      else setShowValidate(true)
    }
    if(stepOpen === 2){
      if(stepsSelectedValue.step2 !== '') setStepOpen(stepOpen + 1)
      else setShowValidate(true)
    }
    if(stepOpen === 3){
      if(stepsSelectedValue.step3 !== '') setStepOpen(stepOpen + 1)
      else setShowValidate(true)
    }
  }

  const runReport = () => {
    setGenerating(true);
    setStepOpen(5)
    // API here
    setTimeout(async () => {
      setGenerating(false)
    }, 5000)
  }

  const initialAnimate = clickBack ? 'backEnter' : clickNext ? 'nextEnter' : false;
  const exitAnimate = clickBack ? 'backExit' : clickNext ? 'nextExit' : false;

  const viewReport = () => {
    history.push('/snapshot-builder/view-report')
  }

  return (
    <>
      <Modal
        show={showBuilderModal}
        onHide={onCancel}
        className="builder-modal"
        backdrop="static"
        keyboard={false}
        centered
      >
        <div className="to-prevent-glitch">prevent glitch</div>
        <div className="content-wrapper">
          <AnimatePresence exitBeforeEnter>
            {stepOpen === 1 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate='nextExit'
                modalTitle='STEP 1: CHOOSE A REPORT TYPE'
              >
                <Modal.Body>
                  {showValidate && <Alert variant="danger" className="w-100 text-center">Please select before you proceed.</Alert>}
                  {step1List && step1List.map((item) => {
                    return (
                      <div className="btn-container" key={`step-${stepOpen}-${item.key}`}>
                        <Button
                          variant="link"
                          onClick={(e) => onSelectValueStep1(e)}
                          id={item.key}
                          className={stepsSelectedValue.step1 === item.key ? 'active' : ''}
                        >
                          {item.icon}{item.label}
                        </Button>
                      </div>
                    )
                  })}
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {stepOpen === 2 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
                modalTitle='STEP 2: CHOOSE A REGION'
              >
                <Modal.Body>
                  {showValidate && <Alert variant="danger" className="w-100 text-center">Please select before you proceed.</Alert>}
                  {step2List && step2List.map((item) => {
                    return (
                      <div className="btn-container" key={`step-${stepOpen}-${item.key}`}>
                        <Button
                          variant="link"
                          onClick={(e) => onSelectValueStep2(e)}
                          id={item.key}
                          className={stepsSelectedValue.step2 === item.key ? 'active' : ''}
                        >
                          {item.icon}{item.label}
                        </Button>
                      </div>
                    )
                  })}
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {stepOpen === 3 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
                modalTitle='STEP 3: CHOOSE TIMEFRAME'
              >
                <Modal.Body>
                  {showValidate && <Alert variant="danger" className="w-100 text-center">Please select before you proceed.</Alert>}
                  {step3List && step3List.map((item) => {
                    return (
                      <div className="btn-container" key={`step-${stepOpen}-${item.key}`}>
                        <Button
                          variant="link"
                          onClick={(e) => onSelectValueStep3(e)}
                          id={item.key}
                          className={stepsSelectedValue.step3 === item.key ? 'active' : ''}
                        >
                          {item.label}
                        </Button>
                      </div>
                    )
                  })}
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {stepOpen === 4 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
                modalTitle='STEP 4: CHOOSE METRICS'
              >
                <Modal.Body>
                  <SearchCheckbox
                    options={optionMetrics}
                    searchPlaceholder='Search for Metric'
                  />
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => runReport()}>Run Report</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {stepOpen === 5 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
              >
                <Modal.Body>
                  {generating
                    ? <motion.div className="body-content"
                      key={generating}
                      variants={animateGenerate}
                      initial='hidden'
                      animate='visible'
                    >
                      <Loader className="icon mb-4" />
                      <h5 className="title">Generating Report…</h5>
                      <p className="note">We’ll send the report to your email after we finish.</p>
                    </motion.div>
                    : <motion.div className="body-content"
                      key={generating}
                      variants={animateGenerate}
                      initial='hidden'
                      animate='visible'
                    >
                      <h5 className="title">Finished!</h5>
                      <Button variant="info" onClick={() => viewReport()}>View Report</Button>
                    </motion.div>
                  }
                </Modal.Body>
              </ContentContainer>
            )}
          </AnimatePresence>
        </div>
      </Modal>
    </>
  )
};

const ContentContainer = ({
  stepOpen,
  initialAnimate,
  exitAnimate,
  modalTitle,
  children,
}) => {
  return (
    <motion.div className={`content-container step-${stepOpen}`}
      key={stepOpen}
      variants={animateStep}
      initial={initialAnimate}
      animate='visible'
      exit={exitAnimate}
    >
      {modalTitle &&
        <Modal.Header>
          <Modal.Title>{modalTitle}</Modal.Title>
        </Modal.Header>
      }
      {children}
    </motion.div>
  )
}


export default BuilderModal;