import React, {useState} from 'react';
import { Container, Row, Col, Button } from 'react-bootstrap';
import Monitor from '../../assets/images/snapshot-monitor.png';
import { ReactComponent as Person } from '../../assets/images/snapshot-person.svg';
import { motion } from 'framer-motion';
import './snapshot.scss';
import BuilderModal from './BuildModal';

const animateContent = {
  hidden: {
    opacity: 0,
    x: '-100px',
  },
  visible: {
    opacity: 1,
    x: '0',
    transition: { duration: 1 }
  }
}

const animateMonitor = {
  hidden: {
    opacity: 0,
    x: '100px',
  },
  visible: {
    opacity: 1,
    x: '0',
    transition: { duration: 1 }
  }
}

const animatePerson = {
  hidden: {
    opacity: 0,
    y: '50px',
  },
  visible: {
    opacity: 1,
    y: '0',
    transition: { duration: .5, delay: .6 }
  }
}

const Builder = () => {
  const [showBuilderModal, setShowBuilderModal] = useState(false);

  const onCancelModal = () => setShowBuilderModal(false);
  const onShowModal = () => setShowBuilderModal(true);

  return (
    <>
      <div className={`page-content`}>
        <Container className="builder-page" fluid>
          <Row>
            <Col xl={7}>
              <div className="inner-wrap">
                <div className="block content-block">
                  <motion.div
                    variants={animateContent}
                    initial='hidden'
                    animate='visible'
                    >
                    <h2 className="title">A fully automated FUSION deck builder at your fingertips</h2>
                    <p className="text">Do you waste time organizing sticky notes, searching your email and apps for to-dos, 
                    and figuring out what to work on first? Then you need one solution to prioritize your tasks, manage your time, and meet your deadlines.</p>
                    <Button onClick={onShowModal}>Create a Snapshot</Button>
                  </motion.div>
                </div>
              </div>
            </Col>
            <Col xl={5}>
              <div className="inner-wrap">
                <div className="block image-block">
                  <motion.img 
                    src={Monitor} 
                    alt="" 
                    className="img-fluid"
                    variants={animateMonitor}
                    initial='hidden'
                    animate='visible'
                  />
                  <motion.div
                    className="icon"
                    variants={animatePerson}
                    initial='hidden'
                    animate='visible'
                  >
                    <Person />
                  </motion.div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
        <BuilderModal
          showBuilderModal={showBuilderModal}
          onCancelModal={onCancelModal}
        />
      </div>
    </>
  )
};

export default Builder;
