import React, {useState} from 'react';
import { useHistory } from "react-router-dom";
import { Container, Row, Col, Card, Button, InputGroup, FormControl, DropdownButton, Dropdown } from 'react-bootstrap';
import { FiSearch } from "react-icons/fi";

import CustomTable from '../../components/customTable/CustomTable';
import MapChart from '../../components/chart/MapChart';


const NavigatorContent = (props) => {
  let history = useHistory();
  const [customerName, setCustomerName] = useState(null)
  const { all_accounts, newly_added_accounts, world_wide_map } = props;
  console.log(all_accounts)
  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'rank',
      text: 'RANK',
      align: 'left',
      headerAlign: 'left',
      classes: 'rank',
      headerClasses: 'rank',
    },
    {
      dataField: 'customer_name',
      text: 'CUSTOMER NAME',
      sort: true,
      align: 'right',
      headerAlign: 'right',
      classes: 'customer-name',
      headerClasses: 'customer-name'
    },
    {
      dataField: 'is_customer',
      text: 'CUSTOMER',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'company_revenue',
      text: 'COMPANY REVENUE',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'mrr',
      text: 'MRR',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    }
  ]

  const defaultSorted = [
    {
      dataField: 'rank',
      order: 'asc', // or desc
    }
  ];

  // For Selecting Row ===
  const handleOnSelect = (row, isSelect, rowIndex) => {
    setCustomerName(row.rank)
    if (isSelect) {
      history.push("/account-navigator/navigator/financials");
    }
  }

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }
  // For Selecting Row ===

  return (
    <Container fluid>
      <Row className="contentDivider">
        <Col md={7}>
          <Card className="revenue-card details-card">
            <Card.Body>
              <h1>Knowledge is power when it comes to customers</h1>
              <p>Do you waste time organizing sticky notes, searching your email and apps for to-dos, and figuring out what to work on first?
              Then you need one solution to
                  </p>
              <div className="bg-white searchable-container">
                <InputGroup>
                  <DropdownButton
                    as={InputGroup.Prepend}
                    title="Customer"
                    id="customer-dropdown"
                  >
                    <Dropdown.Item href="#">Action</Dropdown.Item>
                    <Dropdown.Item href="#">Another action</Dropdown.Item>
                    <Dropdown.Item href="#">Something else here</Dropdown.Item>
                    <Dropdown.Divider />
                    <Dropdown.Item href="#">Separated link</Dropdown.Item>
                  </DropdownButton>
                  <FormControl
                    aria-label="Recipient's username"
                    aria-describedby="basic-addon2"
                  />
                  <InputGroup.Append>
                    <Button variant="search"><FiSearch /></Button>
                  </InputGroup.Append>
                </InputGroup>
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={5}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Newly Added Accounts This Quarter</Card.Title>
              <CustomTable
                tableName='new-accounts-table scrollable'
                data={newly_added_accounts === undefined ? [] : newly_added_accounts}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingTop: "30px" }}>
        <Col md={7}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Worldwide</Card.Title>
              <div style={{ height: "420px ", paddingTop: "2rem" }}>
                <MapChart
                  id={"navigator_map"}
                  largerWidth={true}
                  data={world_wide_map}
                />
              </div>
            </Card.Body>
          </Card>

        </Col>
        <Col md={5}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>All Accounts</Card.Title>
              <CustomTable
                tableName='all-accounts-table scrollable'
                data={all_accounts === undefined ? [] : all_accounts}
                defaultSorted={defaultSorted}
                selectRow={selectRow}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>

  );
};

export default NavigatorContent;