const Content = (props) => {

  var options = { // put in gridstack options here
    disableOneColumnMode: true, // for jfiddle small window size
    float: false
  };
  var grid = GridStack.init(options);

  var count = 0;
  var items = [
    { x: 3, y: 1, width: 1, height: 2 },
    { x: 0, y: 2, width: 2, height: 1 },
    { x: 4, y: 1, width: 1, height: 1 },
    { x: 2, y: 3, width: 3, height: 1 },
    { x: 2, y: 5, width: 1, height: 1 }
  ];

  const addNewWidget = () => {
    var node = items[count] || {
      x: Math.round(12 * Math.random()),
      y: Math.round(5 * Math.random()),
      width: Math.round(1 + 3 * Math.random()),
      height: Math.round(1 + 3 * Math.random())
    };
    grid.addWidget('<div><div class="grid-stack-item-content">' + count++ + '</div></div>', node);
    return false;
  };
  return (
    <div>
      <ul>
        <li><a href="/executive-cockpit/total-revenue/overview">Total Revenue - Overview</a></li>
        <li><a href="/executive-cockpit/total-revenue/growth">Total Revenue - Growth</a></li>
        <li><a href="/executive-cockpit/total-revenue/metro">Total Revenue - Metro</a></li>
        <li><a href="/executive-cockpit/customer-revenue/overview">Customer Revenue - Overview</a></li>
        <li><a href="/executive-cockpit/customer-revenue/growth">Customer Revenue - Growth</a></li>
        <li><a href="/executive-cockpit/customer-revenue/metro">Customer Revenue - Metro</a></li>
        <li><a href="/executive-cockpit/customer-revenue/customer">Customer Revenue - Customer</a></li>
      </ul>
      <h1>gridstack.js base demo for issues</h1>
      <p>Fork and modify me to demonstrate your issue when creating an issue for gridstack.js</p>
      <div><a class="btn btn-default" onClick="addNewWidget()" href="#">Add Widget</a></div>
      <br />

      <div class="grid-stack">
        <div class="grid-stack-item" data-gs-x="0" data-gs-y="0" data-gs-width="2" data-gs-height="2">
          <div class="grid-stack-item-content">item1 </div>
        </div>
        <div class="grid-stack-item" data-gs-x="2" data-gs-y="0" data-gs-width="2" data-gs-height="1">
          <div class="grid-stack-item-content">item2</div>
        </div>
      </div>
    </div>

  )

};

export default Content;


