import React, {useState} from 'react';
import { Form, FormControl } from 'react-bootstrap';
import './searchCheckbox.scss';


const SearchCheckbox = ({ title, options, className, searchPlaceholder }) => {
  const [checkVal, setCheck] = useState([]);
  const [searchVal, setSearchVal] = useState('');

  const onChangeCheck = (e) => {
    const checkValue = checkVal.some(item => item === e);
    const remove = checkVal.filter(item => item !== e);
    if (!checkValue) {
      setCheck([...checkVal, e])
    } else {
      setCheck(remove)
    }
  }

  return (
    <div className={`search-checkbox ${className ? className : '' }`}>
      {title && <div className="title">{title}</div>}
      <div>
        <FormControl
          placeholder={searchPlaceholder}
          onChange={(e) => setSearchVal(e.target.value)}
          value={searchVal}
        />
        <ul className="list-of-checkbox custom-scroll">
          <Form>
            {options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='checkbox'
                id={`check_search-${option.key}`}
                label={option.text}
                checked={checkVal.some(item => item === option.key)}
                onChange={() => onChangeCheck(option.key)}
              />
            )).filter(
              (child) => !searchVal || child.props.label.toLowerCase().includes(searchVal)
            )}
          </Form>
        </ul>
      </div>
    </div>
  );
}
 
export default SearchCheckbox;