import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionAccounts, optionCustomerNavigator } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE_URL } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import NavigatorContent from './NavigatorContent';

const Deployments = () => {
    const [navigatorData, setOverviewData] = useState([])
    const [isLoading, setIsLoading] = useState(true)

    // Axios call
    const fetchData = () => {
        setIsLoading(true)
        axios({
            method: 'get',
            url: `${API_BASE_URL}/account-navigator/navigator.json`,
        }).then(result => {
            setOverviewData(result.data);
            setIsLoading(false)
        }).catch(error => {
            console.log(error);
            setIsLoading(false)
        });
    };

    useEffect(() => {
        fetchData();
    }, []);

    const all_accounts = navigatorData ? navigatorData.all_accounts : [];
    const newly_added_accounts = navigatorData ? navigatorData.newly_added_accounts : [];
    const world_wide_map = navigatorData ? navigatorData.deployment_map : [];

    // responsive dropdowns
    const cardRef = useRef(null);
    const { width } = useWidowResize(cardRef);

    // show all filters
    const [showModal, setShowModal] = useState(false);
    const handleCloseModal = () => {
        setShowModal(false);
    }
    const handleShowModal = () => {
        setShowModal(true)
    };

    // comment area
    const [showComment, setShowComment] = useState(false);
    const handleCloseComment = () => {
        setShowComment(false);
    }
    const handleShowComment = () => {
        setShowComment(true)
    };

    if (isLoading) return <div className="w-100" ref={cardRef}><Loader /></div>;

    return (
        <Container className="account-navigator-page" fluid ref={cardRef}>
            <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
                <FilterContainer
                    className={`${width}`}
                    showComment={showComment}
                    handleShowModal={handleShowModal}
                    handleShowComment={handleShowComment}
                >
                    <div className="filters">
                        <ResponsiveRadioDD
                            id="accounts"
                            wide={width >= 1394 ? true : false}
                            narrow={width < 1394 ? true : false}
                            options={optionAccounts}
                        />
                        <Separator height='39px' />
                        <ResponsiveRadioDD
                            id="include_only"
                            addTitleOnWide
                            wide={width >= 1516 ? true : false}
                            narrow={width < 1516 ? true : false}
                            options={optionCustomerNavigator}
                        />
                    </div>

                    <FilterModal
                        title="Navigator"
                        setShowModal={setShowModal}
                        showModal={showModal}
                        handleCloseModal={handleCloseModal}
                    >
                        <ResponsiveRadioDD
                            id="accounts_modal"
                            options={optionAccounts}
                            visibleInModal
                        />
                        <ResponsiveRadioDD
                            id="include_only_modal"
                            options={optionCustomerNavigator}
                            visibleInModal
                        />
                    </FilterModal>
                </FilterContainer>
                <Comment
                    data={comments}
                    handleCloseComment={handleCloseComment}
                    showComment={showComment}
                />
            </div>
        </Container>
    );
};

export default Deployments;
